    export default {
        props: {
            label: {
                type: String,
                default: '标签'
            },
            value: {
                type: String,
                default: ''
            },
            placeholder: {
                type: String,
                default: '请选择'
            },
            contentStyle: {
                type: String,
                default: ''
            },
            labelStyle: {
                type: String,
                default: ''
            },
            valueStyle: {
                type: String,
                default: ''
            },
			fontStyle: {
				type: String,
				default: ''
			},
            other: {
                type: [Object, Number, String, Array, Boolean],
                default: ''
            }
        },
        methods: {
            selectCell() {
                this.$emit('click', {
                    label: this.label,
                    value: this.value,
                    other:this.other
                })
            }
        },
    }
